body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-image: url(/static/media/burger.e337b95c.jpg);
  background-repeat: repeat;
  max-height: 100%;
  width: 100%;
  border: none
  /* background-color: aqua */
}

.image{
  background-color: rgba(252, 252, 252, 0.829);

}
 /* .column{
margin: 1%
}  */

.common{
  display: inline-block;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.head{
  background-color: rgba(220, 20, 60, .83);

}
.titlesHeader{
  font-size: calc(10px + 2vmin);
  color: #e9c94c;
  /* font-weight: 700; */
  font-family: 'Ultra', serif;
}

.btn{
	outline: none;
	border: none;
	background-color: rgba(220, 20, 60, .85);
	color: #f8d856;
	font-weight: 500;
	border-radius: 10px;
	padding: 5px;
	margin: .5%;
}

.title{
	font-size: 2.3em;
	font-weight: 650;
	color: #1c1c1c;
	margin-top: 30px;
}
.comm{
	padding: 6px;
	border-radius: 10px;
	display: inline-block;
}
.client{
background-color: #ffffff;
border-radius: 10px;
margin-right: 2%;
border-color: #1c1c1c;
border-width: thick;
}
.selection{
	outline: none;
	border: none;
	background-color: #f8d856;
	color: rgba(220, 20, 60);
	font-weight: 700;
	border-radius: 10px;
	padding: 5px;
	margin: 3%;
}
